"use client"; // Required for state and hooks in App Router

import { useState, useEffect } from "react";
import axios from "axios";

export default function Home() {
  const [courses, setCourses] = useState<any[]>([]);
  const [search, setSearch] = useState("");

  useEffect(() => {
    axios.get("http://localhost:5001/api/courses")
      .then((res) => setCourses(res.data))
      .catch((err) => console.error(err));
  }, []);

  return (
    <div className="min-h-screen bg-gray-100 p-6">
      <h1 className="text-3xl font-bold text-center">Course Schedule</h1>

      <input
        type="text"
        placeholder="Search courses..."
        className="w-full p-2 border rounded my-4"
        onChange={(e) => setSearch(e.target.value)}
      />

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
        {courses
          .filter(course => 
            course.courseTitle.toLowerCase().includes(search.toLowerCase()) ||
            course.courseMajor.toLowerCase().includes(search.toLowerCase())
          )
          .map((course) => (
            <div key={course.courseReferenceNumber} className="bg-white p-4 rounded shadow">
              <h2 className="text-xl font-semibold">{course.courseTitle}</h2>
              <p className="text-gray-600">{course.courseMajor}</p>
              <p className="text-sm text-gray-500">CRN: {course.courseReferenceNumber}</p>
            </div>
          ))}
      </div>
    </div>
  );
}